in-bottom: 5px;\r\n    width: 100%;\r\n\r\n  }\r\n\r\n  .app-bar .app-bar__navigation ul li a:hover{\r\n    color: #333;\r\n  }\r\n\r\n/*\r\nHero\r\n*/\r\n /* Add a new CSS class for the animation */\r\n@keyframes fadeIn {\r\n  from {\r\n    opacity: 0;\r\n    transform: translateY(-20px);\r\n  }\r\n  to {\r\n    opacity: 1;\r\n    transform: translateY(0);\r\n  }\r\n}\r\n\r\n.hero {\r\n  text-align: center;\r\n  padding: 15px;\r\n  animation: fadeIn 1s ease-out; \r\n}\r\n\r\n.hero-content {\r\n  display: flex;\r\n  align-items: center;\r\n  text-align: left;\r\n}\r\n\r\n.hero img {\r\n  max-width: 100%;\r\n  max-height: 475px;\r\n  margin-left: 50px;\r\n  margin-right: 100px;\r\n  padding: 15px;\r\n}\r\n\r\n.hero p {\r\n  font-size: 18px;\r\n  margin-top: 20px;\r\n  color: #333;\r\n}\r\n\r\n.explore-button {\r\n  display: inline-block;\r\n  padding: 10px 20px;\r\n  background-color: #b22222;\r\n  color: #fff;\r\n  border: none;\r\n  border-radius: 8px;\r\n  cursor: pointer;\r\n  transition: background-color 0.3s ease;\r\n  margin-top: 8px;\r\n}\r\n\r\n.explore-button a {\r\n  text-decoration: none;\r\n  color: #fff;\r\n  font-size: 15px;\r\n}\r\n\r\n.explore-button:hover {\r\n  background-color: #c63d2f;\r\n}\r\n\r\n/*\r\n  Home\r\n*/\r\n.article-container {\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: center;\r\n}\r\n\r\n.article-header {\r\n    text-align: center;\r\n    margin-top: 30px;\r\n}\r\n.article-header h1{\r\n    color: #b22222;\r\n    font-size: 15px;\r\n}\r\n.article-header p {\r\n    max-width: 400px;\r\n    overflow: hidden;\r\n    color: #333;\r\n    font-size: 15px;\r\n    \r\n}\r\n.article-card {\r\n    display: flex;\r\n    justify-content: center;\r\n    margin-top: 10px;\r\n}\r\n\r\n.article-card img {\r\n    max-width: 100%;\r\n    height: auto;\r\n}\r\n\r\n.article-card .card-item {\r\n    position: relative;\r\n    width: 300px;\r\n    margin: 10px;\r\n    border: 1px solid #ccc;\r\n    border-radius: 8px;\r\n   